// App.jsx
import React, { useState, useCallback, useEffect } from 'react';
import Plot from 'react-plotly.js';
import { evaluate } from 'mathjs';

const App = () => {
    const [lines, setLines] = useState([]);
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    const [plotLayout, setPlotLayout] = useState({
        plot_bgcolor: '#1a1a1a',
        paper_bgcolor: '#1a1a1a',
        font: { color: '#ffffff' },
        xaxis: {
            zeroline: true,
            zerolinecolor: '#404040',
            gridcolor: '#333333',
            range: [-10, 10],
        },
        yaxis: {
            zeroline: true,
            zerolinecolor: '#404040',
            gridcolor: '#333333',
            range: [-10, 10],
        },
        showlegend: false,
        margin: { t: 10, r: 10, b: 40, l: 40 },
        dragmode: 'pan',
    });

    // Generate initial colors for lines
    const generateColor = () => {
        const colors = ['#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', '#FFEEAD', '#D4A5A5', '#9B59B6'];
        return colors[Math.floor(Math.random() * colors.length)];
    };

    // Function to evaluate equation and generate points
    const generatePoints = useCallback((equation, isXEqual, range = [-10, 10], points = 1000) => {
        const step = (range[1] - range[0]) / points;
        const coords = [];

        try {
            for (let i = 0; i <= points; i++) {
                const x = range[0] + (i * step);
                if (isXEqual) {
                    try {
                        // For x = f(y) equations
                        const result = evaluate(equation, { y: x });
                        if (Number.isFinite(result)) coords.push([result, x]);
                    } catch (e) { /* Skip invalid points */ }
                } else {
                    try {
                        // For y = f(x) equations
                        const result = evaluate(equation, { x });
                        if (Number.isFinite(result)) coords.push([x, result]);
                    } catch (e) { /* Skip invalid points */ }
                }
            }
            return coords;
        } catch (error) {
            console.error("Error generating points:", error);
            return [];
        }
    }, []);

    // Add new line
    const addLine = useCallback(() => {
        setLines(prev => [...prev, {
            id: Date.now(),
            equation: 'x',
            isXEqual: false,
            color: generateColor(),
            width: 2,
            type: 'solid',
            visible: true,
            error: null
        }]);
    }, []);

    // Update line properties
    const updateLine = useCallback((id, updates) => {
        setLines(prev => prev.map(line => {
            if (line.id === id) {
                const updated = { ...line, ...updates };
                // Validate equation
                try {
                    const testValue = 1;
                    evaluate(updated.equation, { x: testValue, y: testValue });
                    updated.error = null;
                } catch (error) {
                    updated.error = "Invalid equation";
                }
                return updated;
            }
            return line;
        }));
    }, []);

    // Delete line
    const deleteLine = useCallback((id) => {
        setLines(prev => prev.filter(line => line.id !== id));
    }, []);

    // Reset view
    const resetView = useCallback(() => {
        setPlotLayout(prev => ({
            ...prev,
            xaxis: { ...prev.xaxis, range: [-10, 10] },
            yaxis: { ...prev.yaxis, range: [-10, 10] }
        }));
    }, []);

    // Generate plot data
    const plotData = lines.map(line => {
        if (!line.visible || line.error) return null;

        const coords = generatePoints(
            line.equation,
            line.isXEqual,
            line.isXEqual ?
                [plotLayout.yaxis.range[0], plotLayout.yaxis.range[1]] :
                [plotLayout.xaxis.range[0], plotLayout.xaxis.range[1]]
        );

        return {
            x: coords.map(coord => coord[0]),
            y: coords.map(coord => coord[1]),
            type: 'scatter',
            mode: 'lines',
            line: {
                color: line.color,
                width: line.width,
                dash: line.type === 'dotted' ? 'dot' : 'solid'
            },
            hovertemplate: `${line.isXEqual ? 'x' : 'y'} = ${line.equation}<extra></extra>`
        };
    }).filter(Boolean);

    return (
        <div className="app">
            {/* Header */}
            <header>
                <button className="menu-btn" onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
                    ☰
                </button>
                <h1>Equation Plotter</h1>
                <div className="header-controls">
                    <button onClick={resetView}>Reset View</button>
                </div>
            </header>

            {/* Drawer */}
            <div className={`drawer ${isDrawerOpen ? 'open' : ''}`}>
                <div className="drawer-header">
                    <h2>Equations</h2>
                    <button className="add-btn" onClick={addLine}>+ Add Line</button>
                </div>
                <div className="drawer-content">
                    {lines.map(line => (
                        <div key={line.id} className="line-editor">
                            <div className="line-header">
                                <select
                                    value={line.isXEqual ? 'x' : 'y'}
                                    onChange={(e) => updateLine(line.id, { isXEqual: e.target.value === 'x' })}
                                >
                                    <option value="y">y</option>
                                    <option value="x">x</option>
                                </select>
                                <span>=</span>
                                <input
                                    type="text"
                                    value={line.equation}
                                    onChange={(e) => updateLine(line.id, { equation: e.target.value })}
                                    className={line.error ? 'error' : ''}
                                />
                                <button className="delete-btn" onClick={() => deleteLine(line.id)}>×</button>
                            </div>
                            {line.error && <div className="error-message">{line.error}</div>}
                            <div className="line-controls">
                                <input
                                    type="color"
                                    value={line.color}
                                    onChange={(e) => updateLine(line.id, { color: e.target.value })}
                                />
                                <select
                                    value={line.width}
                                    onChange={(e) => updateLine(line.id, { width: Number(e.target.value) })}
                                >
                                    <option value="1">Thin</option>
                                    <option value="2">Normal</option>
                                    <option value="3">Thick</option>
                                </select>
                                <select
                                    value={line.type}
                                    onChange={(e) => updateLine(line.id, { type: e.target.value })}
                                >
                                    <option value="solid">Solid</option>
                                    <option value="dotted">Dotted</option>
                                </select>
                                <label>
                                    <input
                                        type="checkbox"
                                        checked={line.visible}
                                        onChange={(e) => updateLine(line.id, { visible: e.target.checked })}
                                    />
                                    Visible
                                </label>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Graph */}
            <div className="plot-container">
                <Plot
                    data={plotData}
                    layout={plotLayout}
                    config={{
                        responsive: true,
                        scrollZoom: true,
                        displayModeBar: true,
                        modeBarButtonsToRemove: ['lasso2d', 'select2d'],
                    }}
                    style={{ width: '100%', height: '100%' }}
                    onRelayout={(newLayout) => {
                        if (newLayout['xaxis.range[0]'] !== undefined) {
                            setPlotLayout(prev => ({
                                ...prev,
                                xaxis: {
                                    ...prev.xaxis,
                                    range: [newLayout['xaxis.range[0]'], newLayout['xaxis.range[1]']]
                                },
                                yaxis: {
                                    ...prev.yaxis,
                                    range: [newLayout['yaxis.range[0]'], newLayout['yaxis.range[1]']]
                                }
                            }));
                        }
                    }}
                />
            </div>
        </div>
    );
};

export default App;